* {
  box-sizing: border-box;
}
.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.board {
  border: 4px solid #00bcd4;
  background-color: #00bcd4;
  border-radius: 10px;
  width: 250px;
  height: 250px;
  display: grid;
  grid-template: repeat(3, 1fr) / repeat(3, 1fr);
  padding: 2px;
}
.square {
  border: 2px solid #3f51b5;
  background-color: #3f51b5;
  color: #00bcd4;
  border-radius: 10px;
  font-size: 3rem;
  font-weight: 800;
  cursor: pointer;
  outline: none;
  margin: 2px;
}
.winningSquare {
  background-color: #9c27b0;
  border-color: #9c27b0;
}
.title {
  font-size: 2.5rem;
  font-family: 'Bebas Neue', cursive;
  color: #00bcd4;
}
.player {
  font-family: Arial;
  font-size: 3rem;
  font-weight: 800;
}
.move-list {
  list-style-type: none;
}
.move {
  font-size: 1rem;
  font-family: 'Montserrat', sans-serif;
  border: none;
  border-radius: 5px;
  background-color: #3f51b5;
  color: #00bcd4;
  padding: 4px 8px;
  margin-bottom: 2px;
  min-width: 150px;
  cursor: pointer;
  outline: none;
}
